
.unorderd{
  list-style-type: none;
  display: flex;
  text-align: right;
}

a {
  text-decoration: none;
  font-size: large;
  color: white;
}

.logo {
  display: inline-block;
  width: 70%;
  margin-left: 1%;
  background-color: #3c383c82;
  color: white;
  box-shadow: 0 8px 8px -4px #14e956;
}

.name {
  background-color: #3c383c82;
  color: white;
  margin: 30px;
  padding: 40px; 
  width: 85%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex-wrap: wrap;
}

.list {
  margin: auto;
  padding: 10px;

}

.App-header {
  
  display: flex;
}

.header-link{
  width: 400px;
  height: 70px;
  margin-right: 10px;
  background-color: rgba(0, 0, 0, 0.39);

}

footer { 
  width: 100%;
  background-color: #3c383c82;
  text-align: center;
  margin-top: auto;
  padding: 0px;
  display:flex;
  flex-direction:column;
  justify-content:center;
  align-items:center;
}

.home {
  border: 1px solid black;
  display: flex;
  justify-content: center;
}

img {
  width: 70%;
  height: 85%;
  text-align: center;
  margin: 7px;
}

body {
  background-color: black;
}

.active:hover {
  color: #14e956; /* Change this to the desired color when hovering */
  
}

.links:active, .links:hover {
  color: #14e956;
}

@media only screen and (max-width: 517px) and (min-width: 400px){
  .home {
    border: 1px solid black;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

}

.picture {
  width: 85%;
}

@media only screen and (max-width: 517px) and (min-width: 400px) {
  .picture {
    width: 100%;
    min-width: 517px;
}
}

@media only screen and (max-width: 517px) and (min-width: 400px){
  .image {
    width: 100%;
}
}

@media only screen and (max-width: 517px) and (min-width: 400px) {
  footer {
    width: 100%;
    background-color: #3c383c82;
    text-align: center;
    margin-top: auto;
    padding: 0px;
    display:flex;
    flex-direction:column;
    justify-content:center;
    align-items:center;
    max-width: 400px;
}
}

@media only screen and (max-width: 517px) and (min-width: 400px){
  .name {
    margin: 0px;
    width: 100%;
    min-width: 517px;
}
}


@media only screen and (max-width: 390px) {
  .name {
    /* margin: 0px;
    width: 100%;
    max-width: 390px; */
    min-width: auto;
    padding: 0px;
}
}

@media only screen and (max-width: 390px) {
  .name-header {
   
}
}

@media only screen and (max-width: 390px) {
  footer {
    margin-left: 20px;
}
}

@media only screen and (max-width: 390px) {
  .image {
   
    width: 411px;
}
}

@media only screen and (max-width: 390px) {
  .home {
   
    display: flex;
    flex-direction: column;
    justify-content: center;
    flex-wrap: wrap;
    max-width: 400px;
    margin-left: 20px;
    align-content: center;
    align-items: center;
    flex-wrap: wrap;
}
}

.experience-section {
  padding: 265px 72px 120px;
}

.wrapper {
  width: 100%;
  height: 100%;
  max-width: 1920px;
  margin-right: auto;
  margin-left: auto;
}

.flex-vertical_left_top {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-box-pack: start;
  -webkit-justify-content: flex-start;
  -ms-flex-pack: start;
  justify-content: flex-start;
  -webkit-box-align: start;
  -webkit-align-items: flex-start;
  -ms-flex-align: start;
  align-items: flex-start;
}

.overline_16px {
  display: inline-block;
  margin-bottom: 8px;
  color: #8491a0;
  font-size: 16px;
  line-height: 150%;
  font-weight: 700;
}

.heading_55px {
  display: inline-block;
  width: 100%;
  max-width: 780px;
  margin-top: 0px;
  margin-bottom: 0px;
  color: #fff;
  font-size: 55px;
  line-height: 110%;
}

.experience-grid {
  margin-top: 40px;
  grid-column-gap: 24px;
  -ms-grid-columns: 1fr 1fr 1fr;
  grid-template-columns: 1fr 1fr 1fr;
  -ms-grid-rows: auto;
  grid-template-rows: auto;
}

.w-layout-grid {
  display: -ms-grid;
  display: grid;
  grid-auto-columns: 1fr;
  -ms-grid-columns: 1fr 1fr;
  grid-template-columns: 1fr 1fr;
  -ms-grid-rows: auto auto;
  grid-template-rows: auto auto;
  grid-row-gap: 16px;
  grid-column-gap: 16px;
}

.experience-block {
  padding-right: 48px;
}

.number {
  display: block;
  margin-bottom: 8px;
  color: #8491a0;
  font-size: 117px;
  line-height: 100%;
  font-weight: 400;
}

.heading_24px {
  margin-top: 0px;
  margin-bottom: 16px;
  color: #fff;
  font-size: 24px;
  line-height: 120%;
  font-weight: 700;
}

.paragraph_16px {
  margin-bottom: 0px;
  color: #fff;
  font-size: 16px;
  line-height: 150%;
  text-decoration: none;
}

.experience-block {
  padding-right: 48px;
}



